import React, { Component } from "react";
import { Link } from 'react-router-dom'
import { Navigate } from "react-router-dom";
import FormName from "../../components/FormNameUpdatePage";
import Wrapper_ClickFun from "../../components/wrapperClickFun";
import ChangeHandler from "../../components/ChangeHandler";
import Loader from "../../components/Loader";
import ErrorMessage from "../../components/errorMessage";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus, faXmark } from '@fortawesome/free-solid-svg-icons';
import { Dropdown } from "semantic-ui-react";
import AddIcon from "../../images/add_icon.png"
import { FaRegEdit } from "react-icons/fa";
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';
import { connect } from "react-redux";
import { DateInput } from 'semantic-ui-calendar-react';
import { Icon } from 'semantic-ui-react';
import { RiPhoneFindLine } from "react-icons/ri";
import { Tabs, Tab } from "react-bootstrap";
import Button from 'react-bootstrap/Button';
import { TiDeleteOutline } from "react-icons/ti";
import moment from "moment";
import postHeader from "../../components/postHeader";
import apiError from "../../components/apiError";

const API_form_name = "sampleRegistration";
const List_Path = `/${process.env.REACT_APP_NAME}/${API_form_name}`;
const form_name = "Sample Registration";
const form_type = "Transaction";

const mapStatetoProps = (state) => {
    return {
        adminAccess: state.adminAccess,
        navOrgName: state.navOrgName
    };
};

const StateDic = {
    from_date: "",
    to_date: "",
    service_type: 'Regular',
    loading: false,

};

class CreateUpdatePage extends Component {
    constructor(props) {
        super(props);
        this.state = StateDic;
    }

    handleChange = (event, { name, value }) => {
        if (this.state.hasOwnProperty(name)) {
            this.setState({ [name]: value });
        }
    };

    drpHandleChange = (e) => {
        this.setState({ [e.target.name]: e.target.value });
    };

    downloadReport = async (e) => {
        const requestOptions = {
            method: "POST",
            headers: postHeader(),
            body: JSON.stringify(this.state),
        };
        try {
            let reportURL = `/${process.env.REACT_APP_NAME}/sampleTestResult/downloadReport/`
            const response = await fetch(reportURL, requestOptions);
            if (response.ok) {
                const blob = await response.blob();
                if (blob.size > 0) {
                    let url = window.URL.createObjectURL(blob);
                    let a = document.createElement("a");
                    a.href = url;
                    a.download = "report.csv";
                    a.click();
                    this.setState({
                        loading: false,
                    });
                } else {
                    this.setState({
                        loading: (this.state.loading = false),
                    });
                    alert("Something went wrong, kindly check with administrator");
                }
            }
        } catch (e) {
            let error = e.message;
            apiError.call(this, error);
        }
    }

    render() {
        const { error_message, redirect } = this.state;

        if (redirect) {
            return <Navigate to={List_Path} />;
        }

        return (
            <div className="wrapper">
                <div className="content-wrapper" onClick={Wrapper_ClickFun.bind(this)}>
                    <FormName form_name_list={form_name} form_name={form_name} form_type={form_type} list_page={List_Path} auditTrailView={"Yes"} recordNo={this.state.document_no} tableName={"sample_registration"}></FormName>
                    <Loader load={this.state.loading}></Loader>
                    <ErrorMessage error_message={error_message} err_msg_visible={this.state.err_msg_visible}></ErrorMessage>
                    <div className="row">
                        <div className="col-lg-2 col-md-2 col-sm-2 did-floating-label-content">
                            <select className="did-floating-select" name="service_type" id="service_type" value={this.state.service_type} onChange={this.drpHandleChange} onClick={this.drpHandleChange} onFocus={this.drpHandleChange}>
                                <option value="">---Select---</option>
                                <option value="inward">Inward Report</option>
                                <option value="sr">Sample Register</option>
                                <option value="tr">Test Result</option>
                            </select>
                            <label className="did-floating-label">Report Type</label>
                        </div>
                        <div className="col-lg-2 col-md-2 col-sm-2 did-floating-date-content">
                            <DateInput id="from_date" className="floating_date_input" name="from_date" dateFormat="DD-MMM-YYYY" value={this.state.from_date} iconPosition="left" placeholder="DD-MMM-YYYY" onChange={this.handleChange} icon="calendar alternate" clearable clearIcon={<Icon name="remove" colour="red" className="calender_icon" />} required />
                            <label className="did-floating-label floating_date_label">From Date <span className="mandatory_field">*</span></label>
                            <div className="form-helper helper_hide" id="from_date_helper">Required</div>
                        </div>

                        <div className="col-lg-2 col-md-2 col-sm-2 did-floating-date-content">
                            <DateInput id="to_date" className="floating_date_input" name="to_date" dateFormat="DD-MMM-YYYY" value={this.state.to_date} iconPosition="left" placeholder="DD-MMM-YYYY" onChange={this.handleChange} icon="calendar alternate" clearable clearIcon={<Icon name="remove" colour="red" className="calender_icon" />} />
                            <label className="did-floating-label floating_date_label">To Date</label>
                            <div className="form-helper helper_hide" id="to_date_helper">Required</div>
                        </div>

                        <div className="col-lg-2 col-md-2 col-sm-2 did-floating-date-content">
                            <button class="btn btn-success" id="submitBtn" onClick={this.downloadReport}>Download</button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default connect(mapStatetoProps)(CreateUpdatePage);
